<template>
<div class="container">
  <div class="flex-rCenter">
    <el-form :inline="true" class="">
      <el-form-item label="日期">
        <el-date-picker style="width: 10rem;" type="date" placeholder="选择日期" v-model="addTableData.date"></el-date-picker>
      </el-form-item>
      <el-form-item label="姓名">
        <el-input style="width: 8rem;" v-model="addTableData.name" placeholder="姓名"></el-input>
      </el-form-item>
      <el-form-item label="年龄">
        <el-input style="width: 5rem;" v-model="addTableData.age" placeholder="年龄"></el-input>
      </el-form-item>
      <el-form-item label="住址">
        <el-input style="width: 16rem;" v-model="addTableData.address" placeholder="住址"></el-input>
      </el-form-item>
      <el-form-item label="电子邮箱">
        <el-input style="width: 13rem;" v-model="addTableData.email" placeholder="电子邮箱"></el-input>
      </el-form-item>
      <el-form-item label="工作经验">
        <el-input style="width: 10rem;" v-model="addTableData.workExp" placeholder="工作经验"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleAdd">新增</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchDialogVisible=true">查询</el-button>
      </el-form-item>
    </el-form>
  </div>
  <el-table
      :data="tableData
        .filter(data =>
          (!searchTableData.date ||  data.date.toLowerCase().includes(dateFormat(searchTableData.date).toLowerCase())) &&
          (!searchTableData.name || data.name.toLowerCase().includes(searchTableData.name.toLowerCase())) &&
          (!searchTableData.age || data.age.toLowerCase().includes(searchTableData.age.toLowerCase())) &&
          (!searchTableData.address || data.address.toLowerCase().includes(searchTableData.address.toLowerCase())) &&
          (!searchTableData.email || data.email.toLowerCase().includes(searchTableData.email.toLowerCase())) &&
          (!searchTableData.workExp || data.workExp.toLowerCase().includes(searchTableData.workExp.toLowerCase()))
        )
        .slice((pagination.currentPage-1)*pagination.pageSize, pagination.currentPage*pagination.pageSize)"
      style="width: 100%"
  >
    <el-table-column
        prop="date"
        label="日期"
        sortable
        min-width="150">
      <template #default="scope">
        <i class="el-icon-time"></i>
        <span style="margin-left: 10px">{{ scope.row.date }}</span>
      </template>
    </el-table-column>
    <el-table-column
        label="姓名"
        min-width="100">
      <template #default="scope">
        <div class="name-wrapper">
          <el-tag size="medium">{{ scope.row.name }}</el-tag>
        </div>
      </template>
    </el-table-column>
    <el-table-column
        label="年龄"
        min-width="100">
      <template #default="scope">
        <div class="name-wrapper">
          <el-tag type="success" size="medium">{{ scope.row.age }}</el-tag>
        </div>
      </template>
    </el-table-column>
    <el-table-column
        label="住址"
        min-width="220">
      <template #default="scope">
        <div class="name-wrapper">
          <span style="margin-left: 10px">{{ scope.row.address }}</span>
        </div>
      </template>
    </el-table-column>
    <el-table-column
        label="电子邮箱"
        min-width="180">
      <template #default="scope">
        <i class="el-icon-box"></i>
        <span style="margin-left: 10px">{{ scope.row.email }}</span>
      </template>
    </el-table-column>
    <el-table-column
        label="工作经验"
        min-width="150">
      <template #default="scope">
        <i class="el-icon-apple"></i>
        <span style="margin-left: 10px">{{ scope.row.workExp }}</span>
      </template>
    </el-table-column>
    <el-table-column label="操作" min-width="150">
      <template #default="scope">
        <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-pagination
      background
      layout="total, sizes, prev, pager, next, jumper"
      :total="tableData
        .filter(data =>
          (!searchTableData.date || data.date.toLowerCase().includes(dateFormat(searchTableData.date).toLowerCase())) &&
          (!searchTableData.name || data.name.toLowerCase().includes(searchTableData.name.toLowerCase())) &&
          (!searchTableData.age || data.age.toLowerCase().includes(searchTableData.age.toLowerCase())) &&
          (!searchTableData.address || data.address.toLowerCase().includes(searchTableData.address.toLowerCase())) &&
          (!searchTableData.email || data.email.toLowerCase().includes(searchTableData.email.toLowerCase())) &&
          (!searchTableData.workExp || data.workExp.toLowerCase().includes(searchTableData.workExp.toLowerCase()))
        )
        .length"
      :page-size="pagination.pageSize"
      :page-sizes="[8, 16, 32]"
      :current-page="pagination.currentPage"
      @current-change="currentPageChange"
      @size-change="pageSizeChange"
      style="margin-top: 3rem"
      class="flex-rCenter"
  >
  </el-pagination>
  <el-dialog
      title="编辑"
      v-model="editDialogVisible"
      width="30%"
      :append-to-body="true"
  >
    <el-form class="flex-cCenter">
      <table cellspacing="20">
        <tr>
          <td><label style="margin-right: 12px">日期</label></td>
          <td>
            <el-date-picker style="width: 16rem;" type="date" placeholder="选择日期" v-model="editTableData.date"></el-date-picker>
          </td>
        </tr>
        <tr>
          <td><label style="margin-right: 12px">姓名</label></td>
          <td>
            <el-input style="width: 16rem;" v-model="editTableData.name" placeholder="姓名"></el-input>
          </td>
        </tr>
        <tr>
          <td><label style="margin-right: 12px">年龄</label></td>
          <td>
            <el-input style="width: 16rem;" v-model="editTableData.age" placeholder="年龄"></el-input>
          </td>
        </tr>
        <tr>
          <td><label style="margin-right: 12px">住址</label></td>
          <td>
            <el-input style="width: 16rem;" v-model="editTableData.address" placeholder="住址"></el-input>
          </td>
        </tr>
        <tr>
          <td><label style="margin-right: 12px">电子邮箱</label></td>
          <td>
            <el-input style="width: 16rem;" v-model="editTableData.email" placeholder="电子邮箱"></el-input>
          </td>
        </tr>
        <tr>
          <td><label style="margin-right: 12px">工作经验</label></td>
          <td>
            <el-input style="width: 16rem;" v-model="editTableData.workExp" placeholder="工作经验"></el-input>
          </td>
        </tr>
      </table>
    </el-form>
    <template #footer>
    <span class="dialog-footer">
      <el-button @click="editDialogVisible=false">取 消</el-button>
      <el-button type="primary" @click="handleEditDone">确 定</el-button>
    </span>
    </template>
  </el-dialog>
  <el-dialog
      title="查询"
      v-model="searchDialogVisible"
      width="30%"
      :append-to-body="true"
  >
    <el-form class="flex-cCenter">
      <table cellspacing="20">
        <tr>
          <td><label style="margin-right: 12px">日期</label></td>
          <td>
            <el-date-picker style="width: 16rem;" type="date" placeholder="选择日期" v-model="searchTableData.date"></el-date-picker>
          </td>
        </tr>
        <tr>
          <td><label style="margin-right: 12px">姓名</label></td>
          <td>
            <el-input style="width: 16rem;" v-model="searchTableData.name" placeholder="姓名"></el-input>
          </td>
        </tr>
        <tr>
          <td><label style="margin-right: 12px">年龄</label></td>
          <td>
            <el-input style="width: 16rem;" v-model="searchTableData.age" placeholder="年龄"></el-input>
          </td>
        </tr>
        <tr>
          <td><label style="margin-right: 12px">住址</label></td>
          <td>
            <el-input style="width: 16rem;" v-model="searchTableData.address" placeholder="住址"></el-input>
          </td>
        </tr>
        <tr>
          <td><label style="margin-right: 12px">电子邮箱</label></td>
          <td>
            <el-input style="width: 16rem;" v-model="searchTableData.email" placeholder="电子邮箱"></el-input>
          </td>
        </tr>
        <tr>
          <td><label style="margin-right: 12px">工作经验</label></td>
          <td>
            <el-input style="width: 16rem;" v-model="searchTableData.workExp" placeholder="工作经验"></el-input>
          </td>
        </tr>
      </table>
    </el-form>
    <template #footer>
    <span class="dialog-footer">
      <el-button @click="searchReset">重 置</el-button>
      <el-button type="primary" @click="searchDialogVisible=false">完 成</el-button>
    </span>
    </template>
  </el-dialog>
</div>
</template>

<script>
import { ElMessage } from 'element-plus'

export default {
  name: "BusinessManage",
  data() {
    return {
      pagination: {
        pageSize: 8, // 每页多少条
        currentPage: 1, // 当前页
      },
      editDialogVisible: false,
      searchDialogVisible: false,
      addTableData: {
        id: null,
        date: '',
        name: '',
        age: '',
        address: '',
        email: '',
        workExp: ''
      },
      editTableData: {
        id: null,
        date: '',
        name: '',
        age: '',
        address: '',
        email: '',
        workExp: ''
      },
      searchTableData: {
        id: null,
        date: '',
        name: '',
        age: '',
        address: '',
        email: '',
        workExp: ''
      },
      tableData: [
        {
          id: 1,
          date: '2021-06-29',
          age: 22,
          name: '小虎',
          address: '湖北省武汉市 动物园',
          email: '976678954@qq.com',
          workExp: '无（正在找工作）'
        }
      ]
    }
  },
  methods: {
    dateFormat(date){
      return date?(date.getFullYear())+'-'+((date.getMonth()+1).toString().length==2?(date.getMonth()+1):'0'+(date.getMonth()+1))+'-'+((date.getDate().toString().length==2)?date.getDate():'0'+date.getDate()):''
    },
    getTableData(id){
      return this.tableData.find(data=>data.id === id);
    },
    getNewId(){
      let id = 1;
      while (this.tableData.find(data=>data.id === id)){
        id++;
      }
      return id;
    },
    searchReset(){
      this.searchTableData.date = '';
      this.searchTableData.name = '';
      this.searchTableData.age = '';
      this.searchTableData.address = '';
      this.searchTableData.email = '';
      this.searchTableData.workExp = '';
    },
    handleAdd(){
      this.tableData.push({
        id: this.getNewId(),
        date: this.dateFormat(this.addTableData.date),
        name: this.addTableData.name,
        age: this.addTableData.age,
        address: this.addTableData.address,
        email: this.addTableData.email,
        workExp: this.addTableData.workExp
      });
      ElMessage.success({
        message: '已新增一条数据',
        type: 'success'
      });
    },
    handleEdit(index, row) {
      this.editDialogVisible = true;
      this.editTableData.id = this.getTableData(row.id).id;
      this.editTableData.date = this.getTableData(row.id).date;
      this.editTableData.name = this.getTableData(row.id).name;
      this.editTableData.age = this.getTableData(row.id).age;
      this.editTableData.address = this.getTableData(row.id).address;
      this.editTableData.email = this.getTableData(row.id).email;
      this.editTableData.workExp = this.getTableData(row.id).workExp;
    },
    handleEditDone(){
      let id = this.editTableData.id;
      this.editDialogVisible = false;
      this.getTableData(id).date = this.getTableData(id).date == this.editTableData.date ? this.getTableData(id).date : this.dateFormat(this.editTableData.date);
      this.getTableData(id).name = this.editTableData.name;
      this.getTableData(id).age = this.editTableData.age;
      this.getTableData(id).address = this.editTableData.address;
      this.getTableData(id).email = this.editTableData.email;
      this.getTableData(id).workExp = this.editTableData.workExp;
    },
    handleDelete(index, row) {
      let i = this.tableData.findIndex(data=>data.id === row.id);
      this.tableData.splice(i, 1);
      ElMessage.success({
        message: '已删除一条数据',
        type: 'success'
      });
    },
    currentPageChange(currentPage){
      this.pagination.currentPage = currentPage;
    },
    pageSizeChange(pageSize){
      this.pagination.pageSize = pageSize;
    }
  }
}
</script>

<style scoped lang="scss">
.container{
  width: 100%;
  height: 100%;
  box-shadow: 0px 0px 5px 1px rgba(44, 62, 80, 0.2);
  padding: 1rem;
  background-color: #fff;
}
.el-form{
  .el-form-item{
    margin-bottom: 0px;
  }
  margin-bottom: 22px;
}
</style>